* {
    box-sizing: border-box;
}

html,
body,
#root {
    height: 100%;
    width: 100%;
    margin: 0;
}

@font-face {
    font-family: "Retro Gaming";
    src: url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.eot");
    src: url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.eot?#iefix")
            format("embedded-opentype"),
        url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.woff2")
            format("woff2"),
        url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.woff")
            format("woff"),
        url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.ttf")
            format("truetype"),
        url("https://db.onlinewebfonts.com/t/4c19fc875e7ba1e6831129de3ab5ac0b.svg#Retro Gaming")
            format("svg");
}

body {
    font-family: "Retro Gaming", monaco, Consolas, Lucida Console, monospace,
        -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--white);
    background: var(--black);
}

:root #root {
    --white: #ffffff;
    --black: #000000;
    --primary-color: #008fd6;
    --secondary-color: #999999;
    --background-color: var(--black);
    --surface-color: #1a1a1a;
    --samaritans-background: #b1cdbd;
    --samaritans-color: #2a4f55;
    --tic-stat-toe-background: #770693;
    --tic-stat-toe-color: #09d3ac;
    --header-height: 48px;
}

ul,
ol {
    list-style: none;
    padding: 0;
}

a {
    text-decoration: none;
    color: var(--white);
}
a:focus {
    color: var(--white);
}
a:hover,
a:active {
    color: var(--primary-color);
}

h1,
h2 {
    color: var(--primary-color);
}

h1 {
    font-size: 36px;
}

h2 {
    font-size: 28px;
}

.column {
    display: flex;
    flex-direction: column;
}

.row {
    display: flex;
    flex-direction: row;
}
